@charset "UTF-8";
.MangaPageWrapper {
  margin: 10vmin auto 0 auto;
  width: 100%;
  max-width: 1280px;
  border-radius: 8px;
}
.MangaPageWrapper * {
  margin: 0;
}
.MangaPageWrapper .top {
  border-radius: 8px 8px 0 0;
  display: block;
  background: white;
  height: 400px;
  width: 100%;
  overflow: auto;
}
.MangaPageWrapper .top img {
  border-radius: 8px 0 0 0;
  width: 300px;
  height: 100%;
  float: left;
  margin-right: 10px;
}
.MangaPageWrapper .top .title {
  font-size: 1.5em;
}
.MangaPageWrapper .top .description {
  box-sizing: content-box;
  white-space: pre-wrap;
  text-wrap: avoid;
  width: 100%;
  height: 100%;
  font-size: 1.3em;
}
.MangaPageWrapper .chapterArea {
  border-radius: 0 0 8px 8px;
  padding: 16px 16px;
  width: 100%;
  min-height: 316px;
  background: gainsboro;
  display: grid;
  grid-auto-flow: column;
  grid-template-rows: repeat(10, minmax(max-content, 1fr));
  grid-auto-columns: max-content;
  grid-gap: 0 16px;
  overflow: auto;
  box-sizing: border-box;
}
.MangaPageWrapper .chapterArea .chapterLink {
  box-sizing: border-box;
  line-height: 100%;
  white-space: nowrap;
  font-size: 1.4em;
  padding: 3px 9px;
  background: white;
  text-decoration: none;
  color: black;
  border: none;
}
.MangaPageWrapper .chapterArea .chapterLink:hover {
  opacity: 0.7;
}
@media (orientation: portrait) {
  .MangaPageWrapper {
    width: 100vw;
    font-size: 0.7rem;
    margin-top: 0;
    padding: 5px;
  }
  .MangaPageWrapper .top {
    height: max-content;
    display: block;
  }
  .MangaPageWrapper .top img {
    float: left;
    width: 40vw;
    height: 60vw;
    margin-right: 5px;
  }
  .MangaPageWrapper .top .title {
    font-size: 2em;
  }
  .MangaPageWrapper .chapterArea {
    height: max-content;
    background: gainsboro;
    display: grid;
    grid-auto-flow: row;
    grid-template-columns: repeat(2, minmax(max-content, 1fr));
    grid-template-rows: max-content;
    padding: 8px 8px;
    grid-gap: 0 8px;
    overflow: auto;
    box-sizing: border-box;
  }
}
@media (orientation: landscape) and (max-width: 900px) {
  .MangaPageWrapper {
    font-size: 0.7rem;
    margin: 0;
    padding: 5px;
  }
  .MangaPageWrapper .top {
    height: 250px;
  }
  .MangaPageWrapper .top img {
    width: 200px;
    height: 100%;
    float: left;
    margin-right: 10px;
  }
}

/* Кнопка выпадающего списка */
.dropbtn {
  background-color: #4CAF50;
  color: white;
  width: 60px;
  padding: 8px 16px;
  font-size: 20px;
  border: none;
}

/* Контейнер <div> - необходим для размещения выпадающего содержимого */
.dropdown {
  transform: translate(-100%, 0);
  height: 0;
  position: relative;
  display: inline-block;
  float: left;
}

/* Выпадающее содержимое (скрыто по умолчанию) */
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f1f1f1;
  width: 100%;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

/* Ссылки внутри выпадающего списка */
.dropdown-content * {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  width: 100%;
}

/* Изменение цвета выпадающих ссылок при наведении курсора */
.dropdown-content *:hover {
  background-color: #ddd;
}

/* Показать выпадающее меню при наведении курсора */
.dropdown:hover .dropdown-content {
  display: block;
}

/* Изменение цвета фона кнопки раскрывающегося списка при отображении содержимого раскрывающегося списка */
.dropdown:hover .dropbtn {
  background-color: #3e8e41;
}

